<!--  DOM  -->
<template>
    <div class="followList">
        <top-back :title="''"></top-back>
        <div class="nav">
            <span v-for="(o,i) of navList" :key="i" :class="{'active':i==navNum}" @click="tabFn(i)">{{o}}</span>
        </div>
        <!--  -->
        <template v-if="navNum==0">
            <v-zb v-for="(o,i) of zbList" :key="i" :obj="o" :follow="true" @nofollow="zbNoFollow"></v-zb>
        </template>
        <!--  -->
        <template v-else-if="navNum==1">
            <v-zhongb v-for="(o,i) of zbList" :key="i" :obj="o" :follow="true" @nofollow="zbNoFollow"></v-zhongb>
        </template>
        <!--  -->
        <template v-else>
            <v-qy v-for="(o,i) of zbList" :key="i" :obj="o" :follow="true"  @nofollow="qyNoFollow"></v-qy>
        </template>
    </div>
</template>
<script>
import topBack from '@/components/topback'
import qy from '@/components/qy'
import zbCon from '@/components/zbContent'
import zhongbCon from '@/components/zhongbCon'
export default {
    name: 'followList', // 
    data() {
        return {
            // 
            navList:['','',''],
            navNum:0,
            zbList:[]
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components: {
      'top-back':topBack,
      'v-qy':qy,
      'v-zb':zbCon,
      'v-zhongb':zhongbCon
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.zbAjax();
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        tabFn(i){
            this.navNum=i;
            if(i==0){
                this.zbAjax()
            }else if(i==1){
                this.zhongAjax()
            }else{
                this.qyAjax()
            }
            
        },
        zbAjax(){//
            let that=this;
            this.$http({
                method:'post',
                url:'/userCenter/listCollectionNotice',
                data:{
                    type:'0',
                    pageNo:1,
                    pageSize:1000
                }
            }).then(function(res){
                that.zbList=res.data.data;
            })
        },
        zhongAjax(){//
            let that=this;
            this.$http({
                method:'post',
                url:'/userCenter/listCollectionNotice',
                data:{
                    type:'2',
                    pageNo:1,
                    pageSize:1000
                }
            }).then(function(res){
                that.zbList=res.data.data;
            })
        },
        qyAjax(){
            let that=this;
            this.$http({
                method:'post',
                url:'/userCenter/listCollectionCompany',
                data:{
                    pageNo:1,
                    pageSize:1000
                }
            }).then(function(res){
                that.zbList=res.data.data;
            })
        },
        zbNoFollow(e){//
            let that=this;
            this.$http({
                method:'post',
                url:'/userCenter/cancelCollectionNotice',
                data:{
                    source:e.source,
                    noticeid:e.id,
                }
            }).then(function(res){
                if(that.navNum==0){
                    that.zbAjax()
                }else{
                    that.zhongAjax()
                }
            })
        },
        qyNoFollow(e){//
            let that=this;
            this.$http({
                method:'post',
                url:'/userCenter/cancelCollectionCompany',
                data:{
                    companyid:e.id,
                }
            }).then(function(res){
                that.qyAjax()
            })
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
.followList{
    padding-top: 90px;
    background: #f5f5f5;
    padding-bottom: 0;
}
.nav{
    background: #fff;
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    padding: 28px 32px;
    span{
        width: 168px;
        height: 56px;
        border: 1PX solid #999;
        border-radius: 28px;
        font-size: 28px;
        color: #999;
        text-align: center;
        line-height: 56px;
    }
    .active{
        border-color: #FE6603;
        color: #FE6603;
    }
}

.newNotice{
    background: #fff;
    margin-top: 16px
}
</style>



